.photos-slider {
  position: absolute;
  width: 100%;
  max-width: 1200px;
}
.photos-slider .container {
  padding: 57px 20px 0 50%;

    
  @media (max-width: 950px) {
    padding: 0 25% 0 25%;
  }
    
  @media (max-width: 900px) {
    padding: 0 23% 0 23%;
  }
    
  @media (max-width: 850px) {
    padding: 0 21% 0 21%;
  }
    
  @media (max-width: 800px) {
    padding: 0 19% 0 19%;
  }
    
  @media (max-width: 750px) {
    padding: 0 17% 0 17%;
  }
    
  @media (max-width: 700px) {
    padding: 0 15% 0 15%;
  }
    
  @media (max-width: 650px) {
    padding: 0 13% 0 13%;
  }
    
  @media (max-width: 600px) {
    padding: 0 11% 0 11%;
  }
    
  @media (max-width: 550px) {
    padding: 0 8% 0 8%;
  }
    
  @media (max-width: 500px) {
    padding: 0 5% 0 5%;
  }
    
  @media (max-width: 450px) {
    padding: 0 3% 0 3%;
  }
    
  @media (max-width: 400px) {
    padding: 0 1% 0 1%;
  }
    
  @media (max-width: 350px) {
    padding: 0;
  }
}


.slider {
  list-style-type: none;
  padding: 0;
}
.slider__item {
}


.slider {
  // margin: 50px 0 50px 0;
}

.slider__item-img {
  margin: 0 auto;
}

.slick-slider {
  position: relative;
  // min-width: 0;
  margin: 0;
}
// Ограничевающая оболочка
.slick-list {
  overflow: hidden;
}

// Лента слайдов
.slick-track {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

// Слайд
.slick-slide {
  margin: 0 auto;
}
// Слайд активный
.slick-slide.slick-active {

}
// Слайд основной
.slick-slide.slick-current {
}
// Слайд по центру
.slick-slide.slick-center {}


.slider .slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 10;
  font-size: 0;
  width: 100px;
  height: 100px;
  border: none;
  background-color: transparent;

  @media (max-width: 890px) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: 750px) {
    top: 100%;
  }

  @media (max-width: 700px) {
    top: 103%;
  }
}

.slider .slick-prev {
  width: 50px;
  height: 50px;
  left: 0;
  // background-image: url("../img/svg/arrow-left.svg");
  background-size: 100% 100%;

  // @media (max-width: 890px) {
  //   left: 5%;
  // }

  // @media (max-width: 750px) {
  //   left: 0;
  // }

  // @media (max-width: 700px) {
  //   left: 10%;
  // }
}

// Стрелка вправо
.slider .slick-next {
  width: 50px;
  height: 50px;
  right: 0;
  // background-image: url("../img/svg/arrow-right.svg");
  background-size: 100% 100%;

  @media (max-width: 890px) {
    right: 5%;
  }

  @media (max-width: 750px) {
    right: 0;
  }

  @media (max-width: 700px) {
    right: 10%;
  }
}
// Стрелка неактивная
.slick-arrow.slick-disabled {}

// Точки
.slick-dots {
  display: flex;
  margin: 0 auto;
  position: absolute;
  bottom: 40px;
  left: 50%;
  padding: 0;
  transform: translate(-50%);
}

.slick-dots button {
  // visibility: hidden;
  font-size: 0;
  background-color: gray;
  border: 1px white;
  width: 10px;
  height: 10px;
  border-radius: 100px;
  
  &:hover {
  }
}

.slick-dots li:hover button {
  background-color: rgb(207, 207, 207);
}

.slick-dots li.slick-active button {
  background-color: white;
}

.slick-active {
  border-radius: 100px;
  color: white;
}

.slick-dots li {
  margin: 0 0 0 10px;
  border: none;
  list-style: none;

  // &:before {
  //   content: '*'
  // }
}
.slick-dots-button {}
