.faq {
  width: 100%;
  padding: 0 0 250px 0;

  @media (max-width: 650px) {
    padding: 0 0 50px 0;
  }

}

.faq .container {
  padding: 0 calc(50% + 20px) 0 20px;
  display: flex;
  flex-direction: column;
  
  
  @media (max-width: 950px) {
    padding: 660px 0 0 0;
  }
  
  @media (max-width: 450px) {
    padding: 560px 0 0 0;
  }
  
  @media (max-width: 375px) {
    padding: 520px 0 0 0;
  }
  
  @media (max-width: 350px) {
    padding: 490px 0 0 0;
  }
}

.faq__title {
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 30px;
}

.faq__list {
  max-width: 800px;
  margin: 0 auto;
  list-style-type: none;
  padding: 0;
}

.faq__question {
  margin: 0 0 15px 0;
}

.faq__question-title {
  border: none;
  outline: none;
  width: 100%;
  padding: 15px 15px 15px 40px;
  text-align: left;
  position: relative;
  transition: $transition-fast;
}

.faq__question-title:hover {
  cursor: pointer;
}

.faq__question-title::after {
  content: "+";
  position: absolute;
  top: 15px;
  left: 15px;
}

button.faq__question-title.is-opened::after {
  content: "-";
}

.faq__answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease-in-out;
}

.faq__answer p {
  margin: 10px 0 0 40px;
  padding: 0 10px 0 0;
}
